@import '../../stylesheets/main.scss';

.contact {
  @include content-align;
  height: 100vh;
  color: $white-primary-1;

  h1 {
    font-size: $header-font-size-tertiary;
    color: $white-primary-1;
  }

  .content {
    width: 40%;
    height: 55%;
    margin-left: $content-margin-left-1;

    .content-inner {
      width: 100%;
      line-height: 1.5;
      color: white;

      > * {
        padding: 15px 0px 5px 0; 
        font-size: 18px;
        // font-weight: 500;
      }

      .icon {
        width: 65px;
        height: 65px;
        padding: 5px;
        padding-top: 10px;

        &:hover {
          cursor: pointer;
          color: $primary-color;
          // outline: none;
          transform: translateY(-0.3rem);
          // box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.4);
        }

        &:active, &:focus {
          // outline: none;
          transform: translateY(-0.1rem);
        }
      }

    }
  }
}
